.file-progress{
    position: absolute;
    width: 48px;
    height: 48px;
    left: 50%;
    top: 50%;
    margin: -24px 0 0 -24px;
}

.file-progress-indicator{
    position: absolute;
    width: 42px;
    height: 42px;
    margin: 3px;
    animation: spin 4s linear infinite;
}

.file-progress-background{
    position: absolute;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background: rgba(26, 26, 26, 0.7);
}

.file-progress-circle {
    stroke-linecap: round;
    color: #ffffff;
}

.file-progress-cancel-button{
    height: 18px;
    width: 18px;
    stroke: #ffffff;
    stroke-width: 3px;
    stroke-linecap: round;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -9px;
    margin-top: -9px;
}